.game-area{
  position : relative;
   margin : 50px auto;
   width : 600px;
   height: 600px;
   border : 2px solid #000;
   background-color: aquamarine;
   
}

body{
  background-color: black;
}

.snake-dot{
  position : absolute;
  width : 2%;
  height: 2%;
  background-color: #000;
  border : 1px solid #ffffff;
  z-index: 2

}

.snake-food{
  position : absolute;
  width : 2%;
  height: 2%;
  background: red;
  border: 1px solid #ffffff;
  border-radius: 50%;
  z-index: 1
}